<template>
  <div>
    <div slot="header" @click="back" class="form-header">
      <span class="form-title"><i class="el-icon-arrow-left"></i>返回</span>
    </div>

    <div style="width: 95%;margin: 0 auto;">
      <div class="explain">
        <p> &nbsp;&nbsp;&nbsp;&nbsp;•如何申請退換貨</p>
        <p>
          &nbsp;&nbsp;1.由於質量原因產生的退換貨：<br/>
          &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;至收到商品之日起7天內，向售後服務中心發送郵件至{{email}}，客服會在收到郵件后的1-3個工作日內受理您的請求，退換貨所產生的運費由我方承擔。<br/>
          &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;2.退換貨流程:<br/>
          &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;確認收貨—申請退換貨—客服審核通過—用戶寄回商品—倉庫簽收驗貨—退換貨審核—退款/換貨；<br/>
          &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;退換貨請註明：訂單號、姓名、電話。<br/>
        </p>
        <p>&nbsp;&nbsp;&nbsp;&nbsp;•如何取消訂單</p>
        <p>
          &nbsp;&nbsp;取消訂單需要向售後服務中心發送郵件并注明相关原因，邮件内容应註明您的訂單號、姓名、電話。<br/>
          &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;溫馨提示：本產品的實際使用效果根據個人情況決定，不保證每位用戶都能享受到所宣傳的效果。若有疑問請諮詢在綫客服或通過電子郵箱聯絡我們，本公司享有最終解釋權。<br/>
          &nbsp;&nbsp;&nbsp;&nbsp;支持貨到付款+免郵費+7天鑒賞期！收到貨有任何疑問請聯繫我們在線客服!也可隨時聯繫:{{email}}。同時請附上您的姓名及聯繫方式及訂單編號在郵件當中，或者通過LINE：{{line}}，以便我們能快速的給您及時處理，祝您購物愉快！！！<br>
        </p>

      </div>
    </div>

    <div class="form-box">
      <div class="regions">台湾</div>
      <div style="width: 95%;margin: 15px auto;">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="80px" class="demo-ruleForm" size="small">
          <el-form-item label="姓名" prop="name">
            <el-input v-model="ruleForm.name"></el-input>
          </el-form-item>
          <el-form-item label="聯繫方式" prop="phone">
            <el-input v-model="ruleForm.phone" type="number"></el-input>
          </el-form-item>
          <el-form-item label="纖細地址" required prop="street">
            <el-input v-model="ruleForm.street"></el-input>
          </el-form-item>
          <el-form-item label="郵編" required prop="postCode">
            <el-input v-model="ruleForm.postCode"></el-input>
          </el-form-item>
          <el-form-item label="email">
            <el-input v-model="ruleForm.email"></el-input>
          </el-form-item>
          <el-form-item label="留言">
            <el-input type="textarea" v-model="ruleForm.remarks"></el-input>
          </el-form-item>

        </el-form>
      </div>
    </div>
    <div class="select-set-meal">

    </div>
    <ul class="price-ul">
      <li>
        <span class="left-span">運費</span>
        <span class="right-span">{{hb}}0.00</span>
      </li>
      <li>
        <span class="left-span">商品合計</span>
        <span class="right-span">{{hb}}{{price}}</span>
      </li>
      <li>
        <span class="left-span">實付</span>
        <span class="right-span red-state">{{hb}}{{price}}</span>
      </li>
    </ul>
    <div style="width: 60%; margin: 20px auto;">
      <!--<el-btn class="buy" @click="submitForm('ruleForm')">提交</el-btn>-->
      <el-button style="width: 100%;" type="warning" @click="submitForm('ruleForm')">立即创建</el-button>
    </div>
  </div>
</template>

<script>
  export default {
    data: function () {
      return {
        ruleForm: {
          price :'36.9',
          name: '',
          phone : '',
          street : '',
          email: '',
          amounts :'1',
          postCode : '',
          remarks: '',
          delivery: true,
        },
        rules: {
          name: [
            { required: true, message: '請輸入姓名', trigger: 'blur' },
            { min: 1, max: 10, message: '长度在 1 到 10 个字符', trigger: 'blur' }
          ],
          region: [
            { required: true, message: '请选择活动区域', trigger: 'change' }
          ],
          phone : [
            { required: true, message: '請輸入電話號碼', trigger: 'blur' }
          ],
          street : [
            { required: true, message: '請輸入詳細地址', trigger: 'blur' }
          ],
          postCode : [
            { required: true, message: '請輸入郵編', trigger: 'blur' }
          ]
        },
        hb : [],
        price : [],
        email :[],
        line : []
      }
    },
    rules: {
      name: [
        { required: true, message: '请输入活动名称', trigger: 'blur' },
        { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
      ],

      region: [
        { required: true, message: '请选择活动区域', trigger: 'change' }
      ],

      street: [
        { required: true, message: '请输入地址', trigger: 'blur' }
      ],
      email : [
        { required: true, message: '请输入email', trigger: 'blur' }
      ],
      resource: [
        { required: true, message: '请选择活动资源', trigger: 'change' }
      ],

    },
    created(){
      this.getParams();
    },
    methods: {
      getParams (){
        var priceText = this.$route.params.price;
        var hbText = this.$route.params.hb;
        var emailText = this.$route.params.email;
        var lineText = this.$route.params.line;
        this.hb = hbText;
        this.price = priceText;
        this.email = emailText;
        this.line = lineText;
      },
      submitForm(formName) {
        var relId = this.$route.params.smString;
        var num = this.$route.params.num;
        this.$refs[formName].validate((valid) => {
          var http = 'http://cod.hzhanghuan.com';
          var idArr = window.location.search.split("=");
          if (valid) {

            var formData = {
              "userName": this.ruleForm.name,
              "email": this.ruleForm.email,
              // "productId": '54',
              "productId": idArr[1],
              "phone": this.ruleForm.phone,
              "street": this.ruleForm.street,
              "postCode" : this.ruleForm.postCode,
              "setmealsIds": relId,
              "amounts": num
            };
            var that = this;
            formData = that.$Qs.stringify(formData);
            that.$axios
              .post( http+"/api/v1/productOrder/create", formData,{
                headers: {
                  'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
                }
              })
              .then(response => {
                this.$message('下單成功了!');
                this.$refs[formName].resetFields();
              });
          } else {
            this.$message('請填寫必填信息');
            return false;
          }
        });

      },
      back () {
        this.$router.go(-1);
      }
    }
  }
</script>

<style>

  .form-header {
    height: 45px;
    line-height: 45px;
    box-shadow: 0 2px 6px #f6f6f6;
    margin-bottom: 5px;
  }
  .form-title {
    color: #666;
    cursor: pointer;
    padding-left: 10px;
  }
  .explain h1 {
    font-size: 24px;
    text-align: center;
    color: #222;
    font-weight: 100;
    margin-bottom: 8px;
  }
  .explain p {
    font-size: 12px;
    color: #666;
    margin-top: 8px;
    line-height: 20px;
    text-indent: 16px;
  }
  .form-box {
    margin: 15px 0;
  }
  .regions {
    border-radius: 4px;
    color: #666;
    margin-bottom: 4px;
    line-height: 42px;
    background-color: #f6f6f6;
    text-align: left;
    text-indent: 16px;
  }
  .form-margin {
    margin-bottom: 15px;
  }

  .el-form-item {
    margin-bottom: 10px;
  }
  .submit-box .el-form-item__content {
    margin-left: 0!important;
  }
  .hf{
    background-color: #fff;
    border-radius: 4px;
    border: 1px solid #dcdfe6;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    color: #606266;
    display: inline-block;
    font-size: inherit;
    height: 40px;
    line-height: 40px;
    outline: 0;
    -webkit-transition: border-color .2s cubic-bezier(.645,.045,.355,1);
    transition: border-color .2s cubic-bezier(.645,.045,.355,1);
    width: 100%;
    font-size: 14px;
  }

  /*已选中的商品*/
  .select-set-meal {
    background-color: #f6f6f6;
  }
  .price-ul {
    width: 100%;
  }
  .price-ul li {
    font-size: 12px;
    color: #333;
    overflow: hidden;
    margin: 10px 0;
  }
  .red-state {
    font-weight: bold;
    color: red;
  }
  .left-span {
    float: left;
    margin-left: 10px;
  }

  .right-span {
    float: right;
    margin-right: 10px;
  }
</style>



